.advantages {
    background-color: #fff5f6;
    padding-bottom: 145px;

    &__container {
        margin-left: auto;
        margin-right: auto;
        padding: 0 20px;
        max-width: 480px;
    }

    &__headline {
        display: none;
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        list-style: none;
        gap: 55px;
        justify-content: center;
        padding: 66px 70px 0 20px;
    }

    &__item {
        position: relative;

        &--icon1::before {
            content: " ";
            position: absolute;
            height: 50px;
            width: 33px;
            top: -40px;
            background-image: url(/src/images/advantages/advantages-icon1.png);
            background-repeat: no-repeat;
            background-size: cover;
        }

        &--icon2::before {
            content: " ";
            position: absolute;
            height: 50px;
            width: 50px;
            top: -40px;
            background-image: url(/src/images/advantages/advantages-icon2.png);
            background-repeat: no-repeat;
            background-size: cover;
        }

        &--icon3::before {
            content: " ";
            position: absolute;
            height: 50px;
            width: 44px;
            top: -40px;
            background-image: url(/src/images/advantages/advantages-icon3.png);
            background-repeat: no-repeat;
            background-size: cover;
        }
    }

    &__title {
        font-family: Titan One, sans-serif;
        font-size: 36px;
        font-style: normal;
        font-weight: 400;
        line-height: 1.1;
        letter-spacing: .04em;
        text-align: left;
        color: #d41443;
        margin-bottom: 13px;
        text-transform: uppercase;
    }

    &__description {
        font-family: DM Sans, sans-serif;
        font-size: 12px;
        font-style: normal;
        font-weight: 700;
        line-height: 1.7;
        letter-spacing: 0;
        text-align: left;
        color: #000;
    }
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}